/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 142;
					$space_between_items: 30;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
						
						&:only-of-type {
							width: 100%;
							left: 0;
							
							+ .mkdf-onsale {
								left: 0;
							}
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								max-width: #{$thumb_size}px;
								padding: 0;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
										width: auto;
									}
								}
							}
						}
					}
					
					&.woocommerce-product-gallery--columns-2 {
						
						.woocommerce-product-gallery__image {
							&:not(:first-child) {
								height: 48%;
							}
							&:nth-child(2) {
								top: 0;
							}
							&:nth-child(3) {
								top: 52%;
							}
						}
					}
					
					&.woocommerce-product-gallery--columns-3 {
						
						.woocommerce-product-gallery__image {
							&:not(:first-child) {
								height: 30%;
							}
							&:nth-child(2) {
								top: 0;
							}
							&:nth-child(3) {
								top: 35%;
							}
							&:nth-child(4) {
								top: 70%;
							}
						}
					}
					
					&.woocommerce-product-gallery--columns-4 {
						
						.woocommerce-product-gallery__image {
							&:not(:first-child) {
								height: 22%;
							}
							&:nth-child(2) {
								top: 0;
							}
							&:nth-child(3) {
								top: 26%;
							}
							&:nth-child(4) {
								top: 52%;
							}
							&:nth-child(5) {
								top: 78%;
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 18px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 35px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 5px 0 0;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 13px 0 3px;

			.star-rating {
				color: $first-main-color;
			}

			.woocommerce-review-link {
				display: block;
				margin-top: 9px;
				color: $default-text-color;

				&:hover {
					color: #59815b;
				}
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 16px 0 0;
		
		    p {
			    margin: 0 19%0 0;
		    }
	    }
	
	    table.group_table tr {
		    margin-bottom: 10px;
	    }
	
	    .product_meta {
		    margin: 28px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;

				> span,
				> a {
					font-family: $additional-font;
					font-style: italic;
					font-size: 18px;
				}
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 0;

			.mkdf-social-share-holder.mkdf-list {
				display: flex;
				flex-direction: column-reverse;

				.mkdf-social-title {
					margin: 23px 0 -2px 0;
				}
			}
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 36px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 26px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 42px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0 0 0 24px;
			border-bottom: 1px solid $default-border-color;
			clear: both;
			box-sizing: border-box;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;

				&.active,
				&:hover {
					a {
						color: $default-heading-color;
					}

					a:after {
						width: 100%;
					}
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 13px 55px;
					font-size: 22px;
					line-height: 1.2em;
					font-weight: 700;
					letter-spacing: -0.02em;
					text-transform: uppercase;
					font-family: $default-heading-font;

					@include ipad-landscape {
						margin: 13px 25px;
					}

					&:after {
						content: '';
						width: 0;
						height: 3px;
						background-color: $first-main-color;
						position: absolute;;
						bottom: -15px;
						left: 0;
						transition: .3s;
					}
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			padding: 7px 6.1% 0 6.1%;
			box-sizing: border-box;

			@include phone-landscape {
				padding: 10px 0 0 0;
			}
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 29px 6% 20px;

				@include ipad-landscape {
					margin: 29px 0 20px;
				}
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 20px;
				line-height: 24px;
				margin: 8px 0 24px;
			}

			.star-rating:before,
			.star-rating > span,
			.comment-respond .stars a:before{
				color: $first-main-color;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;

						.woocommerce-review__author {
							font-size: 18px;
						}

						.woocommerce-review__published-date {
							font-family: $additional-font;
							font-style: italic;
							font-size: 18px;
							font-weight: 400;
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 25px;
				
				.comment-reply-title {
					display: block;
					margin: 0 0 10px;
					font-size: 20px;
					font-weight: 700;
					letter-spacing: -0.02em;
					text-transform: uppercase;
					font-family: $default-heading-font;
					color: $default-heading-color;
				}
				
				p {
					margin: 0 0 4px;

					&.comment-form-comment #comment{
						margin-top: 10px;
					}

					&.comment-form-cookies-consent {
						margin: 0 0 17px;
					}
				}
				
				.stars {
					
					> span {
						position: relative;
						display: flex;
						margin: 4px 0;
					}
					
					&.selected {
					
						a {
							@include mkdfWooCommerceSingleProductRatingsStyle(true);
						}
					}
					
					a {
						position: relative;
						display: block;
						color: transparent;
						margin: 0;
						font-size: 0;
						line-height: 1;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}

				.comment-form-email,
				.comment-form-author {
					width: 50%;
					float: left;

					@include phone-landscape {
						width: 100%;
					}

					label {
						position: relative;
						display: block;
					}
				}

				.comment-form-email {
					label {
						margin-left: 28px;

						@include phone-landscape {
							margin-left: 0;
						}
					}
				}

				input[type="text"] {
					width: calc(100% - 28px);

					@include phone-landscape {
						width: 100%;
					}
				}

				input[type="email"] {
					width: calc(100% - 28px);
					margin-left: 28px;

					@include phone-landscape {
						width: 100%;
						margin-left: 0;
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 80px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 30px;
			font-size: 29px;
			position: relative;
		}

		.product {

			.mkdf-pl-text-wrapper {
				margin-bottom: 0;

				@include ipad-landscape {
					margin-bottom: 16px;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */