/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 30px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: #{$sselect-input-height}px;
        font-family: $additional-font;
        font-style: italic;
        font-size: 18px;
        font-weight: 400;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
            text-align: initial;
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */