/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .mkdf-onsale,
    .mkdf-sold,
    .mkdf-new-product {
        @include mkdfWooCommerceMarksGeneralStyle();


        .mkdf-mark-label {
            z-index: 50;
            position: relative;
        }
    }
    
    .mkdf-onsale {
        @include mkdfWooCommerceSaleStyle();
    }
    
    .mkdf-sold {
        @include mkdfWooCommerceOutOfStockStyle();
    }

    .mkdf-new-product {
        @include mkdfWooCommerceNewProductStyle();
    }
}

.mkdf-woo-single-page {
    
    .mkdf-single-product-content {
        
        .mkdf-onsale {
            position: absolute;
            left: 10px;
        }
        
        .mkdf-sold,
        .mkdf-new-product {
            position: absolute;
            right: 10px;
        }
    }
    
    &.mkdf-woo-single-thumb-on-left-side {
        
        .mkdf-single-product-content {
            
            .mkdf-onsale {
                left: 180px;
            }
            
            .mkdf-sold,
            .mkdf-new-product {
                right: 10px;
            }

            .mkdf-sold ~ .mkdf-new-product{
                right: 60px;

            }

            .mkdf-sold,
            .mkdf-new-product,
            .mkdf-onsale {
                top: 10px;
                position: absolute;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */