/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-heading-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 54px;
            width: 57px;
            padding: 0;
            margin: 0 25px 0 0;
            line-height: 52px;
            outline: 0;
            font-family: $additional-font;
            font-style: italic;
            font-size: 15px;
            border-right: none;

            @include phone-landscape {
                width: 35px;
            }
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 25px;
            height: 27px;
            line-height: 25px;
            border-left: 0;
            cursor: pointer;
            transition: color .15s ease-in-out;
            font-size: 17px;
            padding-right: 2px;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .mkdf-quantity-minus {
            bottom: 0;
            border-top: none;

            &:after {
                content: '';
                top: 0;
                left: 0;
                width: calc(100% - 2px);
                height: 1px;
                background-color: $default-border-color;
                position: absolute;
            }
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
        margin: 0 0 20px;
        font-weight: 400;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */