/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .mkdf-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            transition: .5s cubic-bezier(0.66, 0.14, 0.58, 1) .1s;
            overflow: visible;
        }
    }

    .mkdf-shopping-cart-inner {
        @include mkdfRelativeHolderLayout();
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;

        .mkdf-sc-opener-label {
            font-family: $default-heading-font;
            font-weight: 600;
            font-size: 16px;
            text-transform: uppercase;
            margin-right: 13px;
        }

        .mkdf-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;
        }

        .mkdf-sc-opener-count {
            color: #fff;
            border-radius: 100%;
            background-color: $first-main-color;
            font-size: 14px;
            font-family: $additional-font;
            font-style: italic;
            width: 15px;
            height: 15px;
            line-height: 18px;
            text-align: center;
            position: relative;
            top: -6px;
            left: -6px;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-sc-opener-icon {
                font-size: 25px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: #e1e1e1;
    }
}

.mkdf-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: -10px; //to align with right content
    text-align: left;
    box-sizing: border-box;
    max-height: 0;
    pointer-events: none;
    will-change: transform;
    transition: .2s;
    overflow: hidden;
    
    .mkdf-sc-dropdown-inner {
        @include mkdfRelativeHolderLayout();
        width: 300px;
        padding: 31px 26px 25px;
        background-color: #fff;
        box-sizing: border-box;
        box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.12);

        .mkdf-empty-cart {
            color: $default-heading-color;
        }
    }
    
    .mkdf-sc-dropdown-items {
        @include mkdfRelativeHolderLayout();
    }
    
    .mkdf-sc-dropdown-item {
        position: relative;
        display: flex;
        margin-top: 20px;
        align-items: center;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .mkdf-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 80px;
        margin-right: 20px;
        
        a, img {
            display: block;
        }
    }
    
    .mkdf-sc-dropdown-item-content {
        @include mkdfRelativeHolderLayout();
        padding-right: 20px;
    }
    
    .mkdf-sc-dropdown-item-title {
        margin: 0;
        font-family: $default-heading-font;
        font-weight: 600;
        text-transform: uppercase;
    }
    
    .mkdf-sc-dropdown-item-price {
        margin: 3px 0 0;
        @include mkdfWooCommercePriceStyle();

        * {
            line-height: 33px;
            color: #59815b;
        }
    }
    
    .mkdf-sc-dropdown-item-quantity {
        margin: 3px 0 0;
    }
    
    .mkdf-sc-dropdown-item-remove {
        position: absolute;
        top: -7px;
        right: 0;
        font-size: 28px;
        line-height: 1;
    }
    
    .mkdf-sc-dropdown-subtotal {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 22px;
        padding-top: 14px;
        border-top: 1px solid $default-border-color;
        
        > * {
            margin: 0;
        }

        .mkdf-sc-dropdown-total {
            font-family: $additional-font;
            font-style: italic;
            font-size: 18px;
            line-height: 33px;
            font-weight: 600;
            color: $default-text-color;
            margin-top: 3px;
        }

        .mkdf-sc-dropdown-total-amount {
            @include mkdfWooCommercePriceStyle();

            * {
                font-style: italic;
                font-size: 21px;
                color: $first-main-color;
            }
        }
    }
    
    .mkdf-sc-dropdown-button-holder {
        @include mkdfRelativeHolderLayout();
        margin-top: 12px;
    }

    .mkdf-sc-dropdown-button {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSize(huge);
        @include mkdfButtonSolidColor();
        padding: 13px 16px 13px 26px !important;

        > i {
            position: relative;
            display: inline-block;
            vertical-align: sub;
            margin: 0 0 0 4px;
            font-size: 1.9em;
            line-height: inherit;
        }
        
        &:hover {
            @include mkdfButtonSolidHoverColor();
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */