/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include mkdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include mkdfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include mkdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .mkdf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .mkdf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #e1e1e1;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -2px;
                    height: 7px;
                    width: 7px;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    transform: rotate(45deg);
                    
                    &:last-child {
                        transform: translateX(-50%) rotate(45deg);
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 2px 0;
                font-style: italic;
                font-family: $additional-font;

                &:hover {
                    color: #59815b;

                    a {
                        color: #59815b;
                    }
                }

                ul {
                    margin: 0 0 0 10px;
                }

                a {
                    position: relative;
                    display: inline-block;

                    &:before {
                        content: '';
                        position: absolute;
                        left: 0;
                        bottom: 5px;
                        width: 0;
                        height: 1px;
                        background-color: $first-main-color;
                        transition: all .2s ease;
                    }
                    
                    &:hover {
                        color: $first-main-color;

                        &:before {
                            width: 100%;
                            transition: all .2s ease;
                        }
                    }
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .mkdf-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 109px;
                
                a {

                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 86px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 20px 0 3px 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: inline-block;
                    float: none;
                    margin-left: 0;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;

                    .amount {
                        margin-left: 0;
                    }
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            margin-top: -4px;
            
            li {
                
                .product-title {
                    color: $default-heading-color;
                    font-size: 16px;
                    text-transform: uppercase;
                    font-family: $default-heading-font;
                    font-weight: 600;
                }
                
                .amount {
                    color: $default-heading-color;
                    @include mkdfWooCommercePriceStyle();
                    display: inline-block;
                    margin-left: 104px;
                    float: none;

                    &:nth-of-type(2) {
                        margin-left: 0;
                    }
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 10px 1px 0;
                font-family: $additional-font;
                font-style: italic;
                font-size: 18px !important;
                line-height: inherit;
                font-weight: inherit;
                letter-spacing: inherit;
                text-transform: capitalize;
                color: $default-text-color;

                &:after {
                    content: '/';
                    position: absolute;
                    right: -11px;
                }

                &:last-child {
                    &:after {
                        content: '';
                    }
                }

                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: 5px;
                    width: 0;
                    height: 1px;
                    background-color: $first-main-color;
                    transition: all .2s ease;
                }

                &:hover {
                    color: $first-main-color;

                    &:before {
                        width: 100%;
                        transition: all .2s ease;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include mkdfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include mkdfRelativeHolderLayout();
                border: 1px solid $default-border-color;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                transition: color .2s ease-out;
            }
            
            input {
                width: calc(100% - 54px);
                padding: 10px 10px 10px 20px;
                font-size: 18px;
                font-family: $additional-font;
                font-style: italic;
    
                @include mkdfPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 54px;
                padding: 0;
                font-size: 30px;
                cursor: pointer;
                background-color: $first-main-color;
                color: #fff;
                position: absolute;
                right: 0;
                top: -1px;
                height: 56px;
                transition: .3s;
                
                &:hover {
                    color: #ecebe1;
                    background-color: #4f6749;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */