/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: relative;
    display: inline-block;
    top: 0;
    width: auto;
    height: 32px;
    margin: 0 10px;
    padding: 0 6px;
    color: #fff;
    font-family: $additional-font;
    font-size: 15px;
    font-style: italic;
    line-height: 32px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin mkdfWooCommerceSaleStyle() {
    @include mkdfSVGbg(#e9935e);

    .mkdf-mark-label {
        color: #fff;
    }
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    @include mkdfSVGbg(#513834);

    .mkdf-mark-label {
        color: #fff;
    }
}

@mixin mkdfWooCommerceNewProductStyle() {
    @include mkdfSVGbg(#fff);

    .mkdf-mark-label {
        color: $default-heading-color;
    }
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 0;
    line-height: 1;
    color: $default-heading-color;
    font-family: $additional-font;
    font-style: italic;
    
    * {
        font-size: 21px;
        line-height: inherit;
        font-weight: 400;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "Ionicons";
    font-size: 20px;
    line-height: 1;
    letter-spacing: 2px;
    color: #989898;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f3ae\f3ae\f3ae\f3ae\f3ae";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f2fc\f2fc\f2fc\f2fc\f2fc";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle($active-style: false) {
    $single-rating-default-icon: "\f3ae";
    $single-rating-active-icon: "\f2fc";
    
    @if ($active-style) {
    
        &.active {
    
            &:before {
                content: $single-rating-active-icon;
            }
            
            ~ a {
    
                &:before {
                    content: $single-rating-default-icon;
                }
            }
        }
        
        &:not(.active) {
            
            &:before {
                content: $single-rating-active-icon;
            }
        }
    } @else {
        
        &:before {
            content: $single-rating-default-icon;
            @extend %star-ratings;
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */